.Container-title {
    font-family: 'Fjalla One', sans-serif;
    text-decoration-line: underline;
    color: white;
    font-size: 3.5rem;
    width: 100%;
    height: 100%;
}

.Container-main {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
}

.Container-team-info {
    min-width: 100%;
    text-align: justify;
    color: white;
    margin: 20px 0;
}

.container-personal-info {
    flex-basis: 70%;
    color: #000;
}

.Container-info-title {
    border: 3px solid var(--secondary-color);
    width: 100%;
    color: white;
    text-align: center;
    font-size: 1.5rem;
    background-image: linear-gradient(
        to top,
        #ff723b 0%,
        var(--secondary-color) 84%
    );
    border-radius: 10px 10px 0 0;
    padding: 10px 0;
    position: relative;
    top: 8px;
    z-index: -1;
}

.Container-Bild {
    border: 3px solid var(--secondary-color);
    width: 300px;
    margin: auto 0;
    border-radius: 20px;
    overflow: hidden;
    display: grid;
    place-items: center;
    flex-basis: 30%;
}

.videoTag {
    width: 100%;
    height: 45vh;
    object-fit: cover;
}

.Container-info-text {
    border: 3px solid var(--secondary-color);
    background-color: var(--accent-color);
    z-index: 10;
    border-radius: 10px;
    height: 100%;
    color: black;
    padding: 2% 5%;
}

.Container-info-text p {
    margin: 12px 0;
}

.Container-info-text b {
    margin-right: 6px;
}

.Container-info-text ul {
    padding-left: 4%;
}

.Info-text-title {
    text-align: center;
    padding-bottom: 2%;
}

@media screen and (max-width: 991px) {
    .Container-main {
        flex-direction: column;
        height: unset;
    }
    .Container-team-info {
        width: 100%;
    }
    .Container-Bild {
        width: 100%;
        margin-bottom: 20px;
    }
    .Container-info-text {
        text-align: left;
    }
}
