@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One:ital,wght@0,400;0,500;0,600;0,700;1,400&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

:root {
	--primary-color: #080808;
	--secondary-color: #df5a5a;
	--accent-color: #eaeaea;
	--hover-color: #292929;
	--text-color: #fc4444;
}

body {
	background: #4d3436;
	background: linear-gradient(
		to right,
		#4d3436 0%,
		#492427 50%,
		#4d3436 100%
	);
}

.container-main {
    /*border: 5px red solid;*/
    display: flex;
    background: black;
}

/*Anschrift+Kontakt-Container*/
.container-1 {
    /* border: 5px #ccc solid;*/
    padding: 5px 20px;
    width: 60%;
    font-style: 'Poppins', sans-serif;
    color: var(--secondary-color);
    display: flex;
    justify-content: space-between;
    grid-gap: 20px;
    gap: 20px;
    z-index: 1000;
}

/*Copyright-Container*/
.container-2 {
    width: 100%;
    font-style: 'Poppins', sans-serif;
    color: var(--secondary-color);
    margin: auto;
}

/*Logos Container*/
.container-3 {
    /*border: 5px yellow solid;*/
    padding: 5px 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
    grid-gap: 10px;
    gap: 10px;
    z-index: 1000;
}

/*Kontakt*/
.box-1 {
    /*background: dimgray;*/
    list-style-type: none;
    font-size: 13px;
}

/*Anschrift*/
.box-2 {
    /* background: darkslateblue;*/
    list-style-type: none;
    font-size: 13px;
}

/*Copyright zeug ganz unten*/
.box-3 {
    border-top: 1px solid grey;
    background: black;
    font-size: 11px;
    list-style-type: none;
    text-align: center;
    padding: 3px;
}

/*Logos*/
/*.box-4 {
    /*background: black;}*/

a {
    color: var(--secondary-color);
}

@media screen and (max-width: 991px) {
    .container-1 {
        flex-direction: column;
        width: 50%;
    }
    .container-3 {
        width: 50%;
        margin: auto;
    }
}

